.App {
    max-width: 420px;
    margin: 0 auto;
    border-color: lawngreen;

} 

.form {
    border-block-width: 100px;
    
}

* {
    box-sizing: border-box;
}

input[type=text],input[type=email], select, textarea {
    width: 100%;
    padding: 12px;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;
    margin-top: 6px;
    margin-bottom: 16px;
    resize: vertical;
}
input[type=submit] {
    background-color: #4CAF50;
    color: white;
    padding: 12px 20px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
}
input[type=submit]:hover {
    background-color: #45a049;
}

p {
    text-align: center;
    font-weight: 500;
}
.container {
    border-radius: 5px;
    background-color: #f2f2f2;
    padding: 20px;
}
